import React from "react";
// nodejs library to set properties for components
import PropTypes from "prop-types";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";

// @material-ui/icons
import Chat from "@material-ui/icons/Chat";
import VerifiedUser from "@material-ui/icons/VerifiedUser";
import Fingerprint from "@material-ui/icons/Fingerprint";
// core components
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import productStyle from "assets/jss/material-kit-react/views/landingPageSections/productStyle.jsx";


class ProductSection extends React.Component {
  render() {
    const { classes } = this.props;
    return (
      <div className={classes.section}>
        <GridContainer justify="center">
          <GridItem xs={12} sm={12} md={8}>
            <h2 className={classes.title}>あいさつ</h2>
            <h5 className={classes.description}>
              閲覧ありがとうございます。本サイト製作者のフードです。<br/>
              本サイトはぼくの諸々の作品をまとめるための極めて個人的なものです。<br/>
              今までの作品をアルバムに綴じるかように、記録したいと思いました。<br/>
              このSNS時代、商用ならともかく情報をまとめるだけのサイトにそれほど意味があるとは思いません。<br/>
              しかし気が向いたときにでも更新したいです。<br/>
              
              <h3 className={classes.title}>イントロダクション</h3>
              ここ数年、ぼくは同一の世界線上で展開される長い物語に興味を持っています。<br/>              
              心の深くに根を張る物語はある程度「かさ」が必要という気がして、そういった物語を作ることに注力しています。<br/>
              本サイトで紹介する作品は、必然的にその物語に関連したものになっています。<br/>
              「SERIES SITE」では物語そのものとなる小説の紹介、「キャラクター」では物語に出てくるキャラクターの紹介をしています。内容に関してあとで変更・追加するかもしれません。
              未完成の部分が多くあります。 発展途上のサイトということでご了承いただけたらと思います。<br/>
              また物語とは関係無く、自作音楽が投稿されているYoutubeやプログラムコードを投稿したGitHubへのリンクも張っています。大したものは作れなかったのですが、興味があればのぞいてください。<br/>
              
              <h3 className={classes.title}>本ホームページの名前</h3>
              ぼくにとって一つの作品は自分の記憶から作られていると思っています。<br/>
              しかし記憶というものはどこか嘘っぽいものでもあります。<br/>
              「一度失ったものを取り戻すことは永遠にできない。」<br/>
              「思い出したとしてもはっきり見定めることはできない。」<br/>
              こういったことはよく言われていることかもしれませんが、本当にそうだと思います。<br/>
              赤く爛れていく記憶から作られた作品、それらを綴じるサイト、ということで「赤色記憶」という名前をつけました。<br/>
            </h5>
          </GridItem>
        </GridContainer>
      </div>
    );
  }
}

ProductSection.propTypes = {
  classes: PropTypes.object
};

export default withStyles(productStyle)(ProductSection);
